<template>



  <div class="container">

  <header>
    <h3>Araçlar</h3>
  </header>



    <div class="table-wrapper">
      <table class="table-users">
        <tr>
          <th style="width: 40%;">Araç İsmi</th>
          <th style="width: 40%;">Araç Durumu</th>
          <th style="width: 20%;">İşlem</th>
        </tr>
                            <tr v-for="arac in AraclarDizi" :key="arac.key">
                        <td>{{ arac.arac_ismi }}</td>
                        <td>{{ arac.arac_durumu }}</td>
                        <td>
                            <router-link :to="{name: 'aracedit', params: { id: arac.key }}" class="btn btn-edit">
                              <i class="material-icons">create</i>
                            </router-link>
                            
                        </td>
                    </tr>
      </table>
         <router-link to="/aracadd"><button class="btn btn-add">Araç Ekle</button></router-link>
    </div>




  </div>



</template>

<script>
import { db } from "../firebaseDb";

export default {
  data() {
    return {
      AraclarDizi: [],
    };
  },
  created() {
    db.collection("araclar").onSnapshot((snapshotChange) => {
      
      this.AraclarDizi = [];
      snapshotChange.forEach((doc) => {
        this.AraclarDizi.push({
          key: doc.id,
          arac_ismi: doc.data().arac_ismi,
          arac_durumu: doc.data().arac_durumu,
        });
      });
    });
  },
};
</script>

